html {
  overflow: scroll;
}

*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  border: 0;
  box-sizing: border-box;
  font-family: "Playfair Display", serif;
}

a {
  text-decoration: none;
  color: black;
}

p {
  line-height: 20px;
  font-size: 16px;
  font-family: "Barlow Condensed", sans-serif;
  letter-spacing: 1px;
}
ul,
ol,
li {
  list-style: none;
  margin: 0 auto;
  padding: 0;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}

html,
body {
  height: 100%;
  line-height: 1;
  font-size: 14px;
  font-weight: 500;
  color: #000;
  font-family: Montserrat, serif;
  background-color: #fafafa;
}

input {
  outline: none;
}

textarea {
  outline: none;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
  height: 100%;
  width: 100%;
}
::-webkit-input-placeholder {
  /* WebKit browsers */
  color: rgba(107, 107, 107, 0.86);
  font-size: 14px;
  font-family: "Playfair Display", serif;
}
:-moz-placeholder {
  /* Mozilla Firefox 4 to 18 */
  color: rgba(107, 107, 107, 0.86);
  font-size: 14px;
  font-family: "Playfair Display", serif;
}
::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: rgba(107, 107, 107, 0.86);
  font-size: 14px;
  font-family: "Playfair Display", serif;
}
:-ms-input-placeholder {
  /* Internet Explorer 10+ */
  color: rgba(107, 107, 107, 0.86);
  font-size: 14px;
  font-family: "Playfair Display", serif;
}

